<template>
  <div class="favor">
    <van-nav-bar
      left-text="返回"
      title="我的收藏"
      left-arrow
      @click-left="back"
    />

    <van-empty
      class="custom-image"
      image="https://img.yzcdn.cn/vant/custom-empty-image.png"
      description="收藏的商品为空"
      v-if="allLikeData.length == 0"
    />

        <!-- 产品列表 -->
    <div class="product-list-box" v-else>
      
      <div class="product-item" v-for="(item,index) in allLikeData" :key="index" @click="viewProductDetail(item.pid)">
        <div class="product-img-box">
          <img class="auto-img" :src="item.smallImg">
        </div>
        <div class="product-info clearfix">
          <div class="product-info-left fl">
            <div class="product-name">{{item.name}}</div>
            <div class="product-enname">{{item.enname}}</div>
          </div>
          <div class="product-info-right fr">
            <div class="product-price">{{item.price}}</div>
          </div>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import '../assets/less/favor.less'
  export default {
    data(){
      return{
        allLikeData: [],
      }
    },
    created(){
      this.getAllLikeData();
    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      // 获取所有收藏商品的数据
      getAllLikeData(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }
        
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        this.axios({
          method: "GET",
          url: "/findAllLike",
          params: {
            appkey: this.appkey,
            tokenString: tk
          }
        }).then( result => {
          this.$toast.clear();
          if(result.data.code == 2000){
            this.allLikeData = result.data.result;
          }
          // console.log("findAllLike result ==> ",result)
        }).catch( err => {
          this.$toast.clear();
          // console.log("findAllLike err ==> ",err)
        })

      },
      // 查看产品详情
      viewProductDetail(pid){
        this.$router.push({
          name: 'Detail',
          query: {pid}
        });
      }

    }
  }
</script>
